/*@import "~antd/dist/antd.css";*/

body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.fade-enter,
.fade-appear {
    opacity: 0;
    -webkit-transition: opacity 1s ease-in;
    transition: opacity 1s ease-in;
}

.fade-enter-active,
.fade-apper-active {
    opacity: 1;
}

.fade-enter-done {
    opacity: 1;
}

.fade-exit {
    opacity: 1;
}

.fade-exit-active {
    /*opacity: 0;*/
    /*transition: opacity 1s ease-in;*/
    opacity: 1;
}

.fade-exit-done {
    /*opacity: 0;*/
    opacity: 1;
}

/***************************antd样式****************************************/

span.ant-upload-list-item-actions {
    width: 100%;
    height: 100%;
    opacity: 1 !important;
}

i.anticon.anticon-eye-o {
    width: 100% !important;
    height: 100% !important;
}

i.anticon.anticon-eye-o svg {
    width: 0;
    height: 0;
}

i.anticon.anticon-delete {
    position: absolute;
    top: 10px;
    right: 10px;
}

i.anticon.anticon-delete svg {
    opacity: 1;
    color: white;
    background: rgba(0, 0, 0, 0.4);
    border-radius: 10px;
    width: 20px;
    height: 20px;
    padding: 4px;
}

.am-list-content {
    flex: none;
    font-family: PingFangSC-Regular;
    font-size: 12px !important;
    color: #333333;
    letter-spacing: 1.88px;
}

.am-list-extra {
    flex: 1 1;
    font-size: 13px !important;
}

.ant-select-dropdown.ant-select-tree-dropdown.ant-select-dropdown--multiple.ant-select-dropdown-placement-bottomLeft {
    max-height: 260px !important;
}

.ant-select-dropdown.ant-select-tree-dropdown.ant-select-dropdown--multiple.ant-select-dropdown-placement-topLeft {
    max-height: 260px !important;
}

.ant-select-selection--multiple {
    max-height: 100px;
    overflow-y: scroll;
}

span.ant-select-selection.ant-select-selection--multiple {
    border: none;
}

